import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import Fade from 'react-reveal/Fade';
import styled from 'styled-components';

import {Section, Container} from '@components/global';
import SectionHeader from '@ui/sectionheader';
import ImgTxt from '@ui/imgtxt';
import step1 from '@images/art/step_1.svg';
import step2 from '@images/art/step_2.svg';
import step3 from '@images/art/step_3.svg';

const STEPS = [
  {
    header: 'Connect',
    text: 'Connect your Hai device to any of your Bluetooth audio sources (e.g. mobile phone).',
  },
  {
    header: ' Turning on T-mode',
    text: "Turning on the T-mode of your aids. Hai's DBC technology clears the buzzing sound while using the t-mode.",
  },
  {
    header: 'Enjoy and be fashionable',
    text: "Hai's neckcoil sends a high quality audio signal to your hearing aids and/or cochlear implants. All while looking good!",
  },
];

class ImgSwap extends React.Component {
  constructor(props) {
    super(props);
    this.switchImage = this.switchImage.bind(this);
    this.state = {
      currentImage: 0,
      show: true,
      images: [
        step1,
        step2,
        step3
      ]
    };
    this.switchFade = this.switchFade.bind(this);
  }

  switchImage() {
    if (this.state.currentImage < this.state.images.length - 1) {
      this.setState({
        currentImage: this.state.currentImage + 1, 
      });
    } else {
      this.setState({
        currentImage: 0
      });
    }
    return this.currentImage;
  }

  switchFade() {
    this.setState({ 
      show: !this.state.show, 
    });
    return this.show;
  }

  componentDidMount() {
    setInterval(this.switchImage, 2000);
    setInterval(this.switchFade, 1000);
  }

  render() {
    return (
      <div>
        <Fade opposite when={this.state.show}>
          <img src={this.state.images[this.state.currentImage]}
            alt="animation images for introducing the usage of Hai"
          />
        </Fade>
      </div>
    );
  }
}

const Manual = () => (
  <StaticQuery
    query={graphql`
      query {
        Usage: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "usage" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Modularity: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "modularity" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="usage" accent="primary">
        <SectionHeader 
          header="How to use" 
          subheader=""
          bg="inverse"
        />
        <Container>
          <Wrapper>
              <Grid>
                <div>
                       <h3> {STEPS[0].header}</h3>
                      <p>{STEPS[0].text}</p>
                </div>
                <div>
                       <h3> {STEPS[1].header}</h3>
                      <p>{STEPS[1].text}</p>

                </div>
                <div>
                       <h3> {STEPS[2].header}</h3>
                      <p>{STEPS[2].text}</p>
                </div>
              </Grid>
            <CoverImgLayer>
              <Fade>
                <ImgSwap/>
              </Fade>
            </CoverImgLayer>
            <MainImg>
              <Img fluid={data.Usage.childImageSharp.fluid} />
            </MainImg>
          </Wrapper>
        </Container>
        <ImgTxt 
            header="Switch made easy" 
            text="If you use a single hearing aid, you can attach one Hai earbud for your hearing side, and use telecoil for the side with hearing aid. If you want to use Hai without your hearing aids and/or implants, just plug in two earbuds and modify the sound with our app."
            bg="inverse"
          >
          <Img fluid={data.Modularity.childImageSharp.fluid} />
        </ImgTxt>
      </Section>
    )}
  />
);



const Wrapper = styled.div`
  padding: 60px 0 40px 0;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S100};
  width: 100%;
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 0 0 60px 0;
  }
  @media (max-width: ${props => props.theme.screen.sm}) {
    padding: 0 0 40px 0;
  }
  @media (max-width: ${props => props.theme.screen.xs}) {
    padding: 0 0 0 0;
  }
`;

const CoverImgLayer = styled.div`
  position: relative;
  z-index: 2;
  height: 0;
  top: 0px;
  @media (max-width: ${props => props.theme.screen.md}) {
    z-index: 2;
    height: 0;
  }
`;


const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 10px;
  text-align: left;
  align-items: top;
  justify-items: left;
  padding-bottom: 15px;
  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    grid-gap: 15px;
    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const MainImg = styled.div`
  position: relative;
  z-index: 1;
  top: 0px;
`;


export default Manual;
